/* Two doted selector without separation means  - elements that has both 'section' and 'cta' eg: class="section cts" */
section.cta {
    position: relative;
    background-image: url(/static/media/bg-cta.ba2c9a60.jpg);
    background-position: center;
    background-size: cover;
}

/* Two doted selector separated by space
.outside .inside
means element with .inside class descended from an element with class .outside
*/
section.cta .cta-content {
    position: relative;
    z-index: 1;
}

section.cta .cta-content h2 {
    font-size: 50px;
    max-width: 450px;
    margin-top: 0;
    margin-bottom: 25px;
    color: white;
}

@media (min-width: 768px) {
    section.cta .cta-content h2 {
        font-size: 80px;
    }
}

section.cta .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
}

/*
What does a space mean in a CSS selector? i.e. What is the difference between .classA.classB and .classA .classB ? -

.classA.classB refers to an element that has both classes A and B (class="classA classB"); whereas .classA .classB refers to an element with class="classB" descended from an element with class="classA".
*/
/* The below google font is overwriting my fonts in main comopnent, hence commented out */
/* @import url('https://fonts.googleapis.com/css?family=Dosis:300,400,500'); */

@-webkit-keyframes rocket-movement {
    100% {
        -webkit-transform: translate(1200px, -600px);
    }
}

@keyframes rocket-movement {
    100% {
        -webkit-transform: translate(1200px, -600px);
                transform: translate(1200px, -600px);
    }
}

@-webkit-keyframes spin-earth {
    100% {
        -webkit-transform: rotate(-360deg);
        transition: -webkit-transform 20s;
        transition: transform 20s;
        transition: transform 20s, -webkit-transform 20s;
    }
}

@keyframes spin-earth {
    100% {
        -webkit-transform: rotate(-360deg);
        transform: rotate(-360deg);
        transition: -webkit-transform 20s;
        transition: transform 20s;
        transition: transform 20s, -webkit-transform 20s;
    }
}

@-webkit-keyframes move-astronaut {
    100% {
        -webkit-transform: translate(-160px, -160px);
    }
}

@keyframes move-astronaut {
    100% {
        -webkit-transform: translate(-160px, -160px);
        transform: translate(-160px, -160px);
    }
}

@-webkit-keyframes rotate-astronaut {
    100% {
        -webkit-transform: rotate(-720deg);
    }
}

@keyframes rotate-astronaut {
    100% {
        -webkit-transform: rotate(-720deg);
        transform: rotate(-720deg);
    }
}

@-webkit-keyframes glow-star {
    40% {
        -webkit-opacity: 0.3;
    }

    90%,
    100% {
        -webkit-opacity: 1;
        -webkit-transform: scale(1.2);
    }
}

@keyframes glow-star {
    40% {
        -webkit-opacity: 0.3;
        opacity: 0.3;
    }

    90%,
    100% {
        -webkit-opacity: 1;
        opacity: 1;
        -webkit-transform: scale(1.2);
        transform: scale(1.2);
        border-radius: 999999px;
    }
}

.spin-earth-on-hover {

    transition: ease 200s !important;
    -webkit-transform: rotate(-3600deg) !important;
            transform: rotate(-3600deg) !important;
}

